
          @import "@/assets/scss/variables.scss";
        

.aboutMain {
  position: relative;
  width: 90vw;
  height: 90vh;
  overflow: hidden;
  .moving-box {
    position: absolute;
    border-radius: 38%;
    width: 24px;
    height: 24px;
    border: 1px solid black;
    background-color: rgb(128, 172, 84);
  }
  .box {
    border: 1px solid black;
    width: 24px;
    height: 24px;
    border-radius: 38%;
    background-color: red;
    position: absolute;
    transition: transform 0.5s;
  }
}
