
          @import "@/assets/scss/variables.scss";
        

h4 {
  height: 10px;
}
.gameContainer {
  background-image: url("../../assets/tmp/images/back2.webp");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

  .gameBox {
    border: 2pt solid #2c3e50;
    border-radius: 2%;
    backdrop-filter: blur(9.5px);
    background-color: rgba(231, 221, 221, 0.5);
    padding: 1px;

    .gameScores {
      height: 50px;
      .danger {
        font-size: large;
        color: red;
      }
    }
  }
}
.gameScores span {
  font: bold;
  display: block;
}
canvas {
  outline: none; /* 테두리 제거 */
}
.gameOpen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  .modal {
    background: white;
    padding: 20px;
    border-radius: 5px;
    text-align: center;
  }

  .modal button {
    background: #007bff;
    color: white;
    border: none;
    padding: 5px 15px;
    border-radius: 5px;
    cursor: pointer;
    display: inline-block;
    margin-right: 10px;
  }

  .modal button:last-child {
    margin-right: 0;
  }

  .modal button:hover {
    background: #0056b3;
  }
}
